import PositionsIndexRoute from './Index/RouteAsync'
import PositionOneRoute from './One/RouteAsync'

import { suffixSlashRoute } from 'lib/utils'

export const createRoutes = (store) => {
  const indexRoute = PositionsIndexRoute(store)
  return [
    indexRoute,
    suffixSlashRoute(indexRoute),
    PositionOneRoute(store)
  ]
}

export default createRoutes



