import React, { Component, PropTypes } from 'react'
import { graphql } from 'react-apollo'
import gql from 'graphql-tag'
import { MatchWithRoutes } from 'lib/react-router-addons-routes'

class ApolloDemo extends Component {
  render () {
    const { mantle, routes, pattern } = this.props
    return (
      <div>
        <span>{JSON.stringify(mantle)}</span>
        <div>
          {routes.map((route, i) => (
            <MatchWithRoutes key={i} {...route} parentPattern={pattern} />
          ))}
        </div>
      </div>
    )
  }
}

ApolloDemo.propTypes = {
  loading: PropTypes.bool,
  routes: PropTypes.array,
  pattern: PropTypes.string,
  mantle: PropTypes.object
}

const demoQuery = gql`
query {
  mantle {
    parties (pagination: { pageSize: 2 }) {
      edges {
        node {
          partyId
          partyTypeEnumId
        }
      }
      pageInfo {
        totalCount
      }
    }
  }
}
`

const ApolloDemoWithData = graphql(demoQuery, {
  props: ({ ownProps, data: { loading, mantle } }) => {
    return {
      loading,
      mantle
    }
  }
})(ApolloDemo)

export default ApolloDemoWithData



