import React from 'react'
import { Link } from 'react-router'
import { prefixLink } from 'lib/utils'
import './Header.scss'

import Navbar from 'react-bootstrap/lib/Navbar'
import Nav from 'react-bootstrap/lib/Nav'
import NavDropdown from 'react-bootstrap/lib/NavDropdown'

import { NavItem, MenuItem } from 'lib/react-bootstrap-fix'

export const Header = () => (
  <Navbar staticTop role='navigation'>
    <Navbar.Header>
      <Link to={prefixLink('/')}>Home</Link>
    </Navbar.Header>
    <Nav>
      <NavItem eventKey={1} href={prefixLink('/positions')}>职位</NavItem>
      <NavDropdown eventKey={3} title='简历' id='basic-nav-dropdown'>
        <MenuItem eventKey={3.1} href={prefixLink('/resumes')}>面板</MenuItem>
      </NavDropdown>
    </Nav>
  </Navbar>
)

export default Header



