export const prefixLink = (path) => {
  var appBasePath = window.__APP_BASE_PATH__
  if (appBasePath === undefined) appBasePath = ''
  return appBasePath[appBasePath.length - 1] === '/' && path[0] === '/'
    ? `${appBasePath.slice(0, appBasePath.length - 1)}${path}` : `${appBasePath}${path}`
}

export const suffixSlashRoute = (route) => {
  return Object.assign({}, route, { pattern: '/' })
}



