import React, { PropTypes } from 'react'

import OriginalMenuItem from 'react-bootstrap/lib/MenuItem'
import OriginalNavItem from 'react-bootstrap/lib/NavItem'
import OriginalPagination from 'react-bootstrap/lib/Pagination'

export const MenuItem = ({ href, ...props }, { router }) => (
  <OriginalMenuItem onClick={e => {
    e.preventDefault()
    router.transitionTo(href)
  }} href={href} {...props} />
)

MenuItem.propTypes = {
  href: PropTypes.string
}

MenuItem.contextTypes = {
  router: React.PropTypes.any
}

export const NavItem = ({ href, ...props }, { router }) => (
  <OriginalNavItem onClick={e => {
    e.preventDefault()
    router.transitionTo(href)
  }} href={href} {...props} />
)

NavItem.propTypes = {
  href: PropTypes.string
}

NavItem.contextTypes = {
  router: React.PropTypes.any
}

export const Pagination = ({ pageInfo, ...props }) => {
  if (pageInfo) {
    return (
      <OriginalPagination
        activePage={pageInfo.pageIndex + 1}
        items={Math.ceil(pageInfo.totalCount / pageInfo.pageSize)}
        {...props} />
    )
  }
  return (
    <div />
  )
}

Pagination.propTypes = {
  pageInfo: PropTypes.object
}



